.button {
    border: none;
    display: inline-block;
    padding: 8px 16px;
    vertical-align: middle;
    overflow: hidden;
    text-decoration: none;
    color: inherit;
    background-color: inherit;
    text-align: center;
    cursor: pointer;
    white-space: nowrap;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    min-width: 105px;
}

.button:hover,
.button:focus {
    background: #0053ba;
}

.button:focus {
    outline: 1px solid #fff;
    outline-offset: -4px;
}

.button:active {
    transform: scale(0.99);
}

.container {
    display: grid;
    grid-template-columns: min-content 1fr;
    column-gap: 20px;
}

.control {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: space-between;
}

.control > :not(:last-child) {
    margin-right: 5px;
}

.container__table {
    min-width: 583px;
}

.manufacturer-filter__wrapper {
    padding: 10px;
    display: flex;
    border-radius: 10px;
    border: 1px solid black;
    margin-bottom: 10px;
}

.manufacturer-filter__wrapper > :not(:last-child) {
    margin-right: 20px;
}

select {
    padding: 15px 10px 15px 5px;
}

.manufacturer-filter__select {
    font-size: 16px;
}
